@import url(https://fonts.googleapis.com/css?family=Cabin:400,600,700&display=swap);

* {
   padding: 0;
   margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   padding: 0;
   margin: 0;
}

html {
   box-sizing: border-box;
}

body {
   background-color: #fafafa;
   color: #646464;
   font-family: 'Cabin', sans-serif;
   font-size: 18px;
   overflow-y: scroll;
}

ul {
   margin: 0;
   padding: 0;
   list-style: none;
}

a {
   text-decoration: none;
   color: #000000;
}
.blog {
   max-width: 970px;
   margin: 50px auto;
   padding: 30px 40px;
}

.blog__main-title {
   font-size: 44px;
   padding-bottom: 30px;
   margin-bottom: 45px;
   color: #006680;
   border-bottom: 1px solid #dddddd;
}

.open-form-button,
.close-form-button {
   display: block;
   margin-left: auto;
   font-size: 15px;
   font-weight: 700;
   outline: none;
   border: none;
   background-color: transparent;
   color: #0090b4;
   cursor: pointer;
}

.open-form-button:hover,
.close-form-button:hover {
   color: #006680;
}

.close-form-button {
   margin-bottom: 20px;
}
.post-item {
   margin: 20px 0 75px;
}
.post-item__title {
   margin-bottom: 20px;
   font-size: 36px;
   line-height: 46px;
   color: #000000;
}

.post-item__description {
   margin-bottom: 20px;
   font-size: 22px;
   font-weight: lighter;
   line-height: 32px;
}

.post-item__text {
   margin-bottom: 20px;
   line-height: 32px;
}

.post-item__btn {
   font-size: 15px;
   font-weight: 700;
   outline: none;
   border: none;
   background-color: transparent;
   color: #0090b4;
   cursor: pointer;
}

.post-item__buttons-wrapper {
   display: flex;
   justify-content: space-between;
}

.post-item__btn--close-btn:hover,
.post-item__btn--read-more:hover {
   color: #006680;
}

.post-item__btn--remove-btn {
   justify-content: right;
   margin-left: 15px;
   color: #ff3c00;
}

.post-item__date {
   margin-bottom: 20px;
   font-size: 16px;
}

.comment-form {
   max-width: 350px;
   margin: 40px 0;
   border: 1px solid #ddd;
   border-radius: 5px;
   padding: 15px 15px 13px;
   box-sizing: border-box;
}

.comment-form__item {
   margin-bottom: 10px;
}

.form__button--comment-btn {
   font-size: 15px;
   font-weight: 700;
   outline: none;
   border: none;
   background-color: transparent;
   color: #0090b4;
   cursor: pointer;
}

.comment-form__input {
   padding: 10px;
   min-height: 20px;
   width: 100%;
   border-radius: 5px;
   border: 1px solid #ddd;
   font-size: 18px;
   font-family: 'Cabin', sans-serif;
   resize: vertical;
   box-sizing: border-box;
}

.comment-form__input-text {
   min-height: 50px;
}

.comment-form__input:focus {
   outline: 0;
   border: 1px solid #0090b4;
}

.comment-form__item-error-message {
   font-size: 15px;
   font-weight: 700;
   color: #ff3c00;
}

.comment-form__item-error-wrapper {
   display: flex;
   height: 18px;
   justify-content: space-between;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
   color: #c5c3c3;
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
   color: #c5c3c3;
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder {
   color: #c5c3c3;
}

input::placeholder, 
textarea::placeholder {
   color: #c5c3c3;
}
.comment-list {
   width: 350px;
}
.comment-item {
   border-bottom: 1px solid #dddddd;
   margin-bottom: 20px;
   padding-bottom: 12px;
}

.comment-item__wrapper {
   display: flex;
   justify-content: space-between;
}

.comment-item__name {
   color: #000000;
   font-size: 20px;
}

.comment-item__text {
   margin-top: 7px;
   font-size: 16px;
}

.comment-item__right-side-wrapper {
   text-align: right;
}

.comment-item__current-date {
   min-width: 100px;
   font-size: 14px;
   margin-top: 5px;
   margin-bottom: 7px;
}

.comment-item__btn {
   display: inline;
   margin-left: auto;
   font-size: 13px;
   font-weight: 700;
   outline: none;
   border: none;
   background-color: transparent;
   color: #0090b4;
   cursor: pointer;
}

.comment-item__btn:hover {
   color: #ff3c00;
}
.form__item {
   display: flex;
   flex-direction: column;
   margin-bottom: 20px;
}

.form__button {
   font-size: 15px;
   font-weight: 700;
   outline: none;
   border: none;
   background-color: transparent;
   color: #0090b4;
   cursor: pointer;
}

.form__button:hover {
   color: #006680;
}

.form__label {
   font-size: 22px;
   margin-bottom: 10px;
}

.form__input {
   padding: 10px;
   min-height: 20px;
   border-radius: 5px;
   border: 1px solid #ddd;
   font-size: 18px;
   font-family: 'Cabin', sans-serif;
   resize: vertical;
   white-space: pre;
}

.form__input-description {
   min-height: 50px;
}

.form__input-text {
   min-height: 150px;
}

.form__input:focus {
   outline: 0;
   border: 1px solid #0090b4;
}

.form__item-error-message {
   font-size: 15px;
   font-weight: 700;
   color: #ff3c00;
}

.form__item-error-wrapper {
   display: flex;
   height: 20px;
   justify-content: space-between;
}
